/* Works on Chrome, Edge, and Safari */
[data-theme="peach_grey"] *::-webkit-scrollbar {
  width: 6px;
}
[data-theme="peach_grey"] *::-webkit-scrollbar-track {
  background: var(--scroll-bg);
}
[data-theme="peach_grey"] *::-webkit-scrollbar-thumb {
  background-color: var(--scroll-color);
  border-radius: 20px;
}
[data-theme="peach_grey"] * {
  scrollbar-width: thin;
  scrollbar-color: var(--scroll-color) var(--scroll-bg);
}
[data-theme="peach_grey"] .awesomplete .input-with-feedback {
  border: 1px solid var(--border-color);
}
[data-theme="peach_grey"] .form-control {
  border: 1px solid var(--border-color);
}
[data-theme="peach_grey"] .search-bar .awesomplete input {
  background-color: var(--bg-color);
}
[data-theme="peach_grey"] .modal-backdrop {
  background-color: #2c436b !important;
}
[data-theme="peach_grey"] .widget .widget-head .widget-title {
  font-size: var(--text-base);
}
[data-theme="peach_grey"] .comment-box .comment-input-header,
.form-dashboard-section .section-head,
.form-section .section-head,
.head-title {
  font-size: var(--text-base);
  font-weight: 600;
}
[data-theme="peach_grey"] .comment-box .comment-input-container .ql-editor {
  border: 1px solid var(--border-color);
}
[data-theme="peach_grey"] .duration-picker .duration-input {
  background-color: var(--bg-color);
  border: 1px solid var(--border-color);
}
[data-theme="peach_grey"] .control-label {
  font-weight: 500;
}
[data-theme="dark"] {
  --bg-color: var(--gray-900);
  --disabled-control-bg: var(--gray-900);
  --border-color: #1c2126;
  --margin-lg: 15px;
  --text-bold: 600;
  --scroll-bg: #161a1f;
  --scroll-color: #1c2126;
}

[data-theme="light"] {
  --bg-color: #f2f5fa;
  --text-bold: 600;
  --control-bg: #fff;
  --control-bg-on-gray: #d4dcea;
  --disabled-control-bg: #f2f5fa;
  --heading-color: #333;
  --border-color: #e3e8f1;
  --avatar-frame-bg: #dde2ea;
  --margin-lg: 15px;
  --sidebar-select-color: #d4dcea;
  --awesomplete-hover-bg: #d4dcea;
  --scroll-bg: #eef1f5;
  --scroll-color: #d9dfe6;
}

/* Dropdown List Hover */

[data-theme="peach_grey"] .awesomplete > ul > li:hover,
.awesomplete > ul > li[aria-selected="true"] {
  background-color: #ffa00a;
  color: #fff;
}

[data-theme="peach_grey"] [data-label="Create Workspace"] {
  background-color: #ffa00a;
  color: #fff;
}

/* Global CSS */

/* Colours */
[data-theme="peach_grey"] {
  --background-color: #36414c;
  --primary-color: #ffa00a;
  --secondary-color: #7574ff;
  --white-color: #ffffff;
  --black-color: #000000;
  --gray-color: #888;
  --page-head-height: 50px;
  --navbar-height: 50px;
}
[data-theme="peach_grey"] .btn-primary:active,
[data-theme="peach_grey"] .btn-secondary:focus,
[data-theme="peach_grey"] .btn-secondary:active,
[data-theme="peach_grey"] .btn-secondary:focus {
  box-shadow: none !important;
}
[data-theme="peach_grey"] .btn-primary {
  color: #fff !important;
  background-color: var(--primary-color) !important;
  border-radius: 0 !important;
}
[data-theme="peach_grey"] .btn-primary:hover {
  background-color: rgba(255, 120, 10, 0.93) !important;
}

[data-theme="peach_grey"] .page-head,
[data-theme="peach_grey"] .page-head .page-head-content {
  height: var(--page-head-height);
}
[data-theme="peach_grey"] .page-head,
[data-theme="peach_grey"] .page-head .page-head-content {
  top: var(--page-head-height);
}

/* Top Header */
[data-theme="peach_grey"] .navbar {
  background: var(--background-color) !important;
  height: var(--navbar-height);
}
[data-theme="peach_grey"] .navbar-expand {
  background-color: var(--background-color);
}
[data-theme="peach_grey"] #navbar-breadcrumbs > li > a::before {
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  text-decoration: inherit;
  -webkit-font-smoothing: antialiased;
  margin-right: 0.3em;
  display: inline-block;

  font-size: 24px;
  transition: 0.2s;
  position: relative;
  top: 3px;
  content: "\f105";
  margin-right: 10px;
  color: var(--white-color);
}
[data-theme="peach_grey"] #navbar-breadcrumbs a {
  color: var(--white-color);
  text-decoration: none;
}
[data-theme="peach_grey"] header .form-control {
  color: var(--white-color);
}
[data-theme="peach_grey"] header .form-control:focus {
  border: 0;
}
[data-theme="peach_grey"] .search-bar .awesomplete input {
  height: 32px;
}

[data-theme="peach_grey"] header ul#navbar-breadcrumbs {
  margin-top: -8px;
}
[data-theme="peach_grey"] header .form-control:focus {
  background-color: var(--white-color) !important;
}

/* Sidebar */
[data-theme="peach_grey"] .layout-side-section .sidebar-label {
  color: var(--primary-color);
}
[data-theme="peach_grey"] .sidebar-label svg,
[data-theme="peach_grey"] .layout-side-section .sidebar-label .icon {
  stroke: var(--primary-color);
}
[data-theme="peach_grey"] .list-sidebar-button {
  background-color: #c8c4c4 !important;
  box-shadow: none !important;
}
[data-theme="peach_grey"] .list-sidebar-button:active,
[data-theme="peach_grey"] .list-sidebar-button:focus {
  text-decoration: none !important;
  box-shadow: none !important;
}

/* Input Fields */
[data-theme="peach_grey"] input {
  border-radius: 0 !important;
}
[data-theme="peach_grey"] .form-control {
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  background-color: #f2f2f2 !important;
}
[data-theme="peach_grey"] .form-control:focus {
  outline: 0;
  border: 1px solid var(--primary-color);
  box-shadow: none;
  background: rgba(255, 160, 10, 0.14) !important;
}
[data-theme="peach_grey"] .navbar .navbar-nav a.nav-link {
  color: var(--white-color);
}
[data-theme="peach_grey"] span.notifications-seen use,
[data-theme="peach_grey"] symbol#icon-small-down {
  fill: var(--white-color);
}

/* Table */
[data-theme="peach_grey"] .form-grid {
  border-radius: 0;
  border: 1px solid #bbbbbb;
}
[data-theme="peach_grey"] .form-grid .grid-heading-row {
  background-color: #f4f4f4;
}
[data-theme="peach_grey"] .grid-heading-row {
  border-bottom: 1px solid var(--primary-color);
}
[data-theme="peach_grey"] .grid-row > .row .col {
  border-right: 1px solid #d1d8dd;
}
[data-theme="peach_grey"] .form-grid .grid-row:hover {
  background: rgba(255, 152, 0, 0.15);
}

/* Heading Color */
[data-theme="peach_grey"] .head-title,
.form-section .section-head,
.form-dashboard-section .section-head,
.comment-box .comment-input-header {
  color: var(--primary-color);
}

/* Lists */
[data-theme="peach_grey"] .list-subject a {
  text-decoration: none;
  transition: 0.3s;
}
[data-theme="peach_grey"] .list-subject a:hover {
  color: var(--secondary-color);
}

/* Report Table */
[data-theme="peach_grey"]
  .datatable
  .dt-header
  .dt-cell--header
  .dt-cell__content {
  background-color: #f4f4f4;
}
